import { Component } from './Facepalm';
import { routes } from './routes';
import App from './App';

class Router extends Component {
	constructor(host, routes) {
		super(host, routes);
		this.state = {
			routes: routes,
			currentComponent: null
		};



		this.host = host;
		this.routes = routes;
		window.addEventListener("hashchange", this.handleUrlChange.bind(this));
		const app = new App;
		app.init(this.host);
		//console.log(app);
	}

	get path() {
		return window.location.hash.slice(1);
	}

	handleUrlChange() {
		for(let i = 0; i < this.routes.length; i++) {
			if(this.routes[i].href != this.state.currentComponent){
				this.updateState(this.routes[i].href);
			}
		}

	}
	updateState(s) {
		this.state.currentComponent = s;
		console.log(this.state);
	}
	render() {

	}
}
export default Router;